<template>
  <div id="tablet">
    <QueryBox @searchSrldId="searchSrldId"></QueryBox>
    <div class="demo-split">
      <Split v-model="split1" min="200px" max="600px">
        <div slot="left" class="demo-split-pane">
          <Map  ref="map1" :pointData="pointData" @changeredlineid="changeredlineid"></Map>
        </div>
        <div slot="right" class="demo-split-pane">
          <InflectionPointTab :obj="obj" :srld="srld" @ondbclickDingwei="ondbclickDingwei"  @getPointData="getPointData" @mapSizeChange="mapSizeChange"></InflectionPointTab>
        </div>
      </Split>
    </div>
  </div>
</template>

<script>
import Map from '../map/InflectionPoint_map.vue'
import InflectionPointTab from './InflectionPoint_tab.vue'
import QueryBox from './InflectionPoint_query'

export default {
  name: 'Tablet',
  data () {
    return {
      /* 查询框返回查询条件 */
      obj: {},
      pointData: [],
      lon: '',
      lat: '',
      spotTable: true,
      srld: null, // 当前选择的红线id
      split1: 0.6
    }
  },
  components: {
    Map,
    InflectionPointTab,
    QueryBox
  },
  mounted () {
  },
  methods: {
    /* 双击表格修改选中经纬度 */
    ondbclickDingwei (lon, lat, id, layerId) {
      this.$refs.map1.ondbclickDingwei(lon, lat, id, layerId)
    },
    /* 级联查询返回要搜索的数据 */
    searchSrldId (obj) {
      this.obj = obj
    },
    /* 获取全部界桩数据 */
    getPointData (data) {
      this.pointData = data
    },
    /* 控制地图框缩小 */
    mapSizeChange (spotTable) {
      if (spotTable === true) {
        this.spotTable = true
      } else {
        this.spotTable = false
      }
    },
    /**
     * @Description:更改红线id事件的回掉函数
     * @author 张淼
     * @date 2019/4/11
    */
    changeredlineid (id) {
      this.srld = id
    }
  }
}
</script>

<style lang='scss' scoped rel='stylesheet/scss'>
  #tablet{
    position: relative;
    .map_box{
      position:absolute;
      width:60%;
      top: 55px;
      bottom: 0;
    }
    .cascader_box{
      position: absolute;
      left:70px;
      top:70px;
    }
    .spotTableBox{
      position: absolute;
      right:0;
      top:55px;
      width:40%;
    }
    .demo-split{
      height: calc(100% - 65px);
      padding: 0 15px 15px;
    }
    .demo-split-pane{
      height: 100%;
    }
  }
</style>
